import React from 'react';
const { Carousel } = require('react-carousel-minimal');

interface Props {
  data: object;
}

export function CarouselComponent(props: Props) {
  const captionStyle = {
    fontSize: '2em',
    fontWeight: 'bold',
  };
  const slideNumberStyle = {
    fontSize: '20px',
    fontWeight: 'bold',
  };
  return (
    <Carousel
      data={props.data}
      time={2000}
      width="850px"
      height="500px"
      captionStyle={captionStyle}
      radius="10px"
      slideNumber={true}
      slideNumberStyle={slideNumberStyle}
      captionPosition="bottom"
      automatic={true}
      dots={true}
      pauseIconColor="white"
      pauseIconSize="40px"
      slideBackgroundColor="darkgrey"
      slideImageFit="cover"
      thumbnails={true}
      thumbnailWidth="100px"
      thumbnailHeight="100px"
      style={{
        textAlign: 'center',
        maxWidth: '850px',
        maxHeight: '500px',
        margin: '40px auto',
      }}
    />
  );
}
